.App {
  height: 100%; 
  text-align: center;
}

.wrap {
  padding: 80px 0 100px 0; 
  background-color: rgb(184, 164, 184); 
}

.calculator {
  width: 350px; 
  height: 400px; 
  border: 5px solid black; 
  margin: 0 auto; 
}

#formular {
  height: 8.75%; 
  border: 1px solid black; 
  background-color: black; 
  color: orange; 
  font-family: sans-serif;
  font-size: 18px; 
  text-align: right; 
}

.formularDisplay {
  width: 100%; 
  margin: 0; 
  text-transform: uppercase; 
}

#input {
  height: 10%; 
  width: 100%;  
  border: 1px solid black; 
  background-color: black; 
  color: white; 
  font-family: sans-serif;
  font-size: 28px; 
  text-align: right; 
  text-transform: uppercase; 
}

.row {
  height: 16.25%; 
}

button {
  height: 65px; 
  font-size: 17px; 
  color: white; 
  font-family: 'Orbitron', sans-serif;
  cursor: pointer; 
}

button:hover {
  -webkit-transform: scale(1.05);
      -ms-transform: scale(1.05);
          transform: scale(1.05); 
}

.big-buttons {
  width: 75%; 
}

.middle-buttons {
  width: 50%; 
}

.operator-buttons, .number-buttons {
  width: 25%; 
}

.equal-buttons {
  width: 25%; 
  background-color: rgb(18, 84, 110); 
}

.operator-buttons {
  background-color: rgb(151, 147, 147); 
}

.number-buttons, #zero {
  background-color: rgb(85, 83, 83); 
}

#clear {
  background-color: rgb(155, 43, 3); 
}

.footnote {
  font-family: 'Orbitron', sans-serif;
  font-size: 13px; 
  color: rgb(38, 38, 92); 
  padding-top: 40px; 
  text-align: center; 
}

@media(max-width: 550px) {
  .calculator {
    width: 300px; 
  }
}

@media(min-height: 650px) {
  .wrap {
    padding-top: 150px; 
    padding-bottom: 200px; 
  }
}

@media(min-width: 1400px) {
  .wrap {
    padding-top: 200px; 
    padding-bottom: 300px; 
  }
}

@media(min-width: 1600px) {
  .wrap {
    padding-top: 100px; 
  }
  .calculator {
    width: 650px; 
    height: 805px; 
  }
  button {
    height: 130px; 
    font-size: 30px; 
  }
  #input {
    font-size: 65px; 
  }
  #formular {
    font-size: 45px; 
  }
  .footnote {
    font-size: 23px; 
    padding-top: 100px; 
  }
}

@media(min-width: 2000px) {
  .wrap {
    padding-top: 300px; 
  }
  .footnote {
    font-size: 30px; 
    padding-top: 140px; 
  }
}

@media(min-height: 800px) {
  .wrap {
    padding-top: 150px; 
  }
  .footnote {
    padding-top: 100px; 
  }
}


